.about-plans__description {
  margin: 0;
  margin-bottom: 10px;
}

.about-plans__types-descr {
  margin: 0;
  margin-bottom: 10px;
}

.about-plans__types-list {
  @include list-reset();
  display: flex;
  flex-direction: column;
}

.about-plans {
  margin-bottom: 40px;
  padding-top: 30px;
  position: relative;

  &::before {
    content: "";
    width: 100vw;
    height: 2px;
    display: block;
    background-color: $border-color;
    overflow: hidden;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
  }
}

@media (min-width: $tablet-width) {
  .about-plans {
    margin-bottom: 60px;
  }
  
  .about-plans__description {
    text-align: center;
    font-size: 16px;
  }

  .about-plans__types-descr {
    font-size: 16px;
    margin-bottom: 0;
  }

  .about-plans__types-list {
    flex-direction: row;
    flex-grow: 1;
  }

  .about-plans__types {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    min-height: 500px;
    position: relative;
  }
}

@media (min-width: $desktop-width) {
  .about-plans {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 65px;

    &::before {
      display: none;
    }
  }

  .about-plans__description {
    font-size: 18px;
    margin-bottom: 20px;
  }

  .about-plans__types-descr {
    font-size: 18px;
    line-height: 18px;
  }
}